import React, { Fragment } from 'react';

const RowReporte = ({row, styleAdd}) =>{
    const isFloat = (n) => {
        if( n === 0 )
            return true;
        if( Number.isInteger( n ) )
            return true;
        return Number(n) === n && n % 1 !== 0;
    }

    const formatCurrency = (num) =>{
        if( num < 0 )
            num = - num;
        var formatter = new Intl.NumberFormat('es-MX', {
            style: 'currency',
            currency: 'MXN',
        });
        return formatter.format( num );
    }

    const getDateFormat = (str) =>{
        if( str === null )
            return str;
        else if( str.includes("/") ){
            let arrStr = str.split("/");
            if( arrStr[1].length === 1 )
                arrStr[1] = "0" + arrStr[1];
        
            if( arrStr[0].length === 1 )
            arrStr[0] = "0" + arrStr[0];

            return arrStr[1] + "/" + arrStr[0] + "/" + arrStr[2];
        }
        return str;
    }

    return (
        <Fragment>
            <tr>
            {
                row.map(
                    (field, i) =>{
                        let aling = "";
                        if( i === 0 )
                            field = getDateFormat( field )
                        if( i === 4 || i === row.length )
                            aling = " text-center"
                        if( isFloat( field ) && i > 4 ){
                            field = formatCurrency( field );
                            aling = " text-right";
                        }
                        return <td className={"row-" + i + " " + styleAdd + aling} key={i}>{field}</td>
                    }
                )
            }
            </tr>
        </Fragment>
    )
}

export default RowReporte;