import React, { Fragment, useState } from 'react';
import InputFile from '../components/InputFile';
import { Button } from '@material-ui/core';
import { useHistory } from 'react-router-dom';
import './styles/Index.css';

const Index = () => {
    const [file, setFile] = useState({ name: '' });
    const [form, setForm] = useState({});
    const [tablesView, setTables] = useState({});
    const history = useHistory();
    const [cargado, setCargado] = useState(false);

    const onLoad = async (e) => {
        setFile(e.target.files[0]);

        let format = new FormData();
        format.append("file", e.target.files[0]);
        let config = {
            method: 'POST',
            body: format
        }
        let res = await fetch("/report/getData.php", config);
        let data = await res.json();
        let tables = [];
        let table = [];

        data.forEach(element => {
            table.push(element);
        });
        llenarFormulario(table);
        tables.push(table.slice(21, table.length));
        setTables(tables);
    }

    const llenarFormulario = (table) => {
        setForm({ ...form, nombreHospital: table[0][1], direccionHospital: table[1][1], rfcHospital: table[2][1], numeroPaciente: table[3][1], episodio: table[4][1], nombrePaciente: table[5][1], domicilioPaciente: table[6][1], ciudad: table[7][1], estado: table[8][1], telefono: table[9][1], fechaNacimiento: table[10][1], tipoInternado: table[11][1], fechaHospitalizacion: table[12][1], horaHospitalizacion: table[13][1], fechaEgreso: table[14][1], horaEgreso: table[15][1], habitacion: table[16][1], medico: table[17][1], deudor: table[18][1], diagnostico: table[19][1] });
        setCargado( true );
    }

    const generarGrupos = () => {
        history.push("/reporte", { tables: tablesView, form: form });
    }

    return (
        <Fragment>
            <div className="container mt-5 text-center">
                <h5>Selecciona el archivo:</h5>
                <h6>{file.name}</h6>
                <InputFile onLoad={onLoad} />
            </div>

            {
                cargado ?
                    <Fragment>
                        <div className="container mt-5 mb-5 text-center">
                            <Button variant="contained" color="secondary" onClick={generarGrupos}>
                                Generar reporte
                            </Button>
                        </div>
                    </Fragment>
                    :
                    void (0)
            }
        </Fragment>
    )
}

export default Index;

