import React, { Fragment } from 'react';
import Table from 'react-bootstrap/Table';
import Row from '../components/Row';
import './styles/Table.css';

const TableReporte = ({ rows }) => {
    return (
        <Fragment>
            <Table>
                <thead>
                    <tr>
                        <th>Fecha</th>
                        <th>Tipo prestación</th>
                        <th>Prestación</th>
                        <th>Descripción</th>
                        <th>Cantidad</th>
                        <th>Precio</th>
                        <th>Importe</th>
                    </tr>
                </thead>
                <tbody>
                {
                    rows.map(
                        (row, i) =>{
                            if( rows.length === i + 1 )
                                return <Row key={"A" + i} styleAdd="font-weight-bold totales" row={row} />
                            return <Row key={"A" + i} row={row}  styleAdd=""/>
                        }
                    )
                }
                </tbody>
            </Table>
        </Fragment>
    )
}

export default TableReporte;