import React, { Fragment } from 'react';
import Logo from '../images/logo.png';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import './styles/TableHead.css';

const TableHead = ({ form }) => {
    
    const getDateFormat = (str) =>{
        if( str === null)
            return;
        let array = String( str ).split("/");
        console.log( array );
        if( String( array[0] ).length === 1 )
            array[0] = "0" + array[0];
            
        if( String(array[1]).length === 1 )
            array[1] = "0" + array[1];

        return String( array[1] + "/" + array[0] + "/" + array[2] );
    }

    return (
        <Fragment>
            <div className="text-right">
                <img src={Logo} alt="logo-oma" width="100px" />
            </div>
            <div className="text-center">
                <h4>{form.nombreHospital}</h4>
                <h6>{form.direccionHospital}</h6>
                <h6>RFC {form.rfcHospital}</h6>
            </div>
            <Container className="table-head">
                <Row>
                    <Col>
                        <Row>
                            <Col xs={5}>
                                <p>número paciente</p>
                            </Col>
                            <Col>
                                <p>{form.numeroPaciente}</p>
                            </Col>
                        </Row>
                        <Row>
                            <Col xs={5}>
                                <p>episodio</p>
                            </Col>
                            <Col>
                                <p>{form.episodio}</p>
                            </Col>
                        </Row>
                        <Row>
                            <Col xs={5}>
                                <p>paciente</p>
                            </Col>
                            <Col>
                                <p>{form.nombrePaciente}</p>
                            </Col>
                        </Row>
                        <Row>
                            <Col xs={5}>
                                <p>domicilio</p>
                            </Col>
                            <Col>
                                <p>{form.domicilioPaciente}</p>
                            </Col>
                        </Row>
                        <Row>
                            <Col xs={5}>
                                <p>ciudad</p>
                            </Col>
                            <Col>
                                <p>{form.ciudad}</p>
                            </Col>
                        </Row>
                        <Row>
                            <Col xs={5}>
                                <p>estado</p>
                            </Col>
                            <Col>
                                <p>{form.estado}</p>
                            </Col>
                        </Row>
                        <Row>
                            <Col xs={5}>
                                <p>teléfono</p>
                            </Col>
                            <Col>
                                <p>{form.telefono}</p>
                            </Col>
                        </Row>
                        <Row>
                            <Col xs={5}>
                                <p>Fecha de nacimiento</p>
                            </Col>
                            <Col>
                                <p>{getDateFormat(form.fechaNacimiento)}</p>
                            </Col>
                        </Row>
                    </Col>
                    <Col>
                        <Row>
                            <Col xs={5}>
                                <p>{form.tipoInternado}</p>
                            </Col>
                            <Col>
                                <p>{getDateFormat( form.fechaHospitalizacion )}</p>
                            </Col>
                            <Col>
                                <p>Hora</p>
                            </Col>
                            <Col>
                                <p>{form.horaHospitalizacion}</p>
                            </Col>
                        </Row>
                        <Row>
                            <Col xs={5}>
                                <p>Admisión/egreso</p>
                            </Col>
                            <Col>
                                <p>{getDateFormat( form.fechaEgreso )}</p>
                            </Col>
                            <Col>
                                <p>Hora</p>
                            </Col>
                            <Col>
                                <p>{form.horaEgreso}</p>
                            </Col>
                        </Row>
                        <Row>
                            <Col xs={5}>
                                <p>Habitación</p>
                            </Col>
                            <Col>
                                <p>{form.habitacion}</p>
                            </Col>
                        </Row>
                        <Row>
                            <Col xs={5}>
                                <p>Médico</p>
                            </Col>
                            <Col>
                                <p>{form.medico}</p>
                            </Col>
                        </Row>
                        <Row>
                            <Col xs={5}>
                                <p>Tipo paciente</p>
                            </Col>
                            <Col>
                                <p>Convenio</p>
                            </Col>
                        </Row>
                        <Row>
                            <Col xs={5}>
                                <p>Pagador</p>
                            </Col>
                            <Col>
                                <p>ATLANTIS OPERADORA SERVICIOS DE</p>
                            </Col>
                        </Row>
                        <Row>
                            <Col xs={5}>
                                <p>Convenio</p>
                            </Col>
                            <Col>
                                <p>ATLANTIS OPERADORA SERVICIOS DE</p>
                            </Col>
                        </Row>
                        <Row>
                            <Col xs={5}>
                                <p>Deudor</p>
                            </Col>
                            <Col>
                                <p>{form.deudor}</p>
                            </Col>
                        </Row>
                        <Row>
                            <Col xs={5}>
                                <p>Diagnostico</p>
                            </Col>
                            <Col>
                                <p>{form.diagnostico}</p>
                            </Col>
                        </Row>
                    </Col>
                </Row>
            </Container>
        </Fragment>
    )
}

export default TableHead;